import {create} from 'zustand';

// Типы состояния
interface ChatState {
    messages: string[]; // История сообщений от ИИ
    lastQuestionDate: Date | null; // Дата последнего вопроса
    currentQuestion: string; // Текущий вопрос
    addMessage: (message: string) => void; // Функция для добавления сообщения
    setCurrentQuestion: (question: string) => void; // Функция для установки текущего вопроса
    setLastQuestionDate: (date: Date) => void; // Функция для установки даты последнего вопроса
    aiAnswer: string;
}

// Создание zustand store
export const useChatStore = create<ChatState>((set) => ({
    messages: [],
    lastQuestionDate: null,
    currentQuestion: '',
    aiAnswer: 'Greetings, lost soul. If you want to get the right answers, ask the right questions.',
    addMessage: (message) => set((state) => ({ messages: [...state.messages, message] })),
    setCurrentQuestion: (question) => set({ currentQuestion: question }),
    setLastQuestionDate: (date) => set({ lastQuestionDate: date }),
}));
